@import url(https://fonts.googleapis.com/css?family=Open+Sans|Indie+Flower|Noto+Serif);
.text-title {
  font: 400% Indie Flower, cursive;
  line-height: 55px;
  padding-top: 15px; }

.text-heading {
  font: 100% Open Sans, sans-serif; }

.text-center {
  text-align: center; }

.text-full {
  text-align: justify; }

.text-primary {
  color: #4AB19A !important; }

.text-secondary {
  color: #545454 !important; }

.text-tertiary {
  color: #ECA042 !important; }

h1, h2, h3, h4, h5, h6 {
  font: 200% Open Sans, sans-serif; }

.text-small {
  font-size: 75%; }

.w-100 {
  width: 100%;
  max-width: 900px; }

.h-100 {
  height: 100%; }

.mb-0 {
  margin-bottom: 0; }

.mt-0 {
  margin-top: 0; }

.ml-0 {
  margin-left: 0; }

.mr-0 {
  margin-right: 0; }

.mx-0 {
  margin-left: 0;
  margin-right: 0; }

.my-0 {
  margin-top: 0;
  margin-bottom: 0; }

.mt-auto {
  margin-top: auto; }

.mb-auto {
  margin-bottom: auto; }

.ml-auto {
  margin-left: auto; }

.mr-auto {
  margin-right: auto; }

.mx-auto {
  margin-left: auto;
  margin-right: auto; }

.my-auto {
  margin-top: auto;
  margin-bottom: auto; }

.pb-0 {
  padding-bottom: 0; }

.pt-0 {
  padding-top: 0; }

.pl-0 {
  padding-left: 0; }

.pr-0 {
  padding-right: 0; }

.px-0 {
  padding-left: 0;
  padding-right: 0; }

.py-0 {
  padding-top: 0;
  padding-bottom: 0; }

.d-block {
  display: block; }

.d-flex {
  display: -webkit-flex;
  display: flex; }

.justify-content-center {
  -webkit-justify-content: center;
          justify-content: center; }

.justify-content-between {
  -webkit-justify-content: space-between;
          justify-content: space-between; }

.justify-content-around {
  -webkit-justify-content: space-around;
          justify-content: space-around; }

.align-items-center {
  -webkit-align-items: center;
          align-items: center; }

.flex-column {
  -webkit-flex-direction: column;
          flex-direction: column; }

.p-relative {
  position: relative; }

.p-absolute {
  position: absolute; }

body {
  font: 100% Noto Serif, serif;
  font-size: 1.15rem;
  margin: 0;
  background-color: #fff; }

header {
  box-sizing: border-box;
  padding: 15px;
  background-color: #eaf6f4; }
  header .icon {
    width: 65px;
    height: 65px;
    background-color: #4AB19A;
    border-radius: 50%; }

footer {
  padding: 15px;
  background-color: #eaf6f4; }

a {
  color: #4AB19A;
  padding: 0 10px;
  text-decoration: none; }
  a:hover, a.active {
    color: #15332c; }

.container {
  max-width: 900px;
  margin-left: auto;
  margin-right: auto; }

main section {
  padding: 15px;
  margin-bottom: 35px; }
  main section p {
    max-width: 550px; }
  main section .icon {
    width: 100px;
    height: 100px;
    background-color: #eaf6f4;
    border-radius: 50%; }

section#home {
  background-color: #eaf6f4;
  height: 50vh; }
  @media screen and (min-width: 750px) {
    section#home {
      height: calc(100vh - 95px); } }

